import './App.css';
import Page1 from './pages/Page1';
import Page2 from './pages/Page2';

const Border = ({ className }) => <div className={`absolute w-full h-4 bg-black ${className}`} />;

const KacperImg = () => (
  <div
    id="kacper-img"
  />
);
const MaskImg = () => (
  <div
    id="mask-img"
  />
);

const Background = () => (
  <div className="absolute z-0 w-full h-full" id="background">
    <Border className="top-0" />
    <MaskImg />
    <KacperImg />
    <Border className="bottom-0" />
  </div>
);

function App() {
  const handleScroll = (dir) => {
    window.scrollTo({
      top: dir === 'down' ? document.body.scrollHeight : 0,
      behavior: 'smooth',
    });
  };

  return (
    <div
      className="relative w-screen max-w-screen-2xl h-full mx-auto"
    >
      <Background />
      <main className="relative z-10" id="foreground">
        <Page1 handleScroll={handleScroll} />
        <Page2 handleScroll={handleScroll} />
      </main>
    </div>
  );
}

export default App;
